import {mapState } from 'vuex'
export default {
  computed: {
    ...mapState({
      posts: state=>state.post.posts
    }),
    post(){
      let result =  this.posts.find(o=>o.id==this.$route.params.post_id) || 
        {
          cover: "http://service.zashare.org/img/expo/2017/3.jpg",
          title: " !",
          subtitle: "ourse  ",
          established_time: "2017/10/20-22",
          content: `<p>2018ourse 00 12<br><br></p><div class="quote"><br></div><h2></h2><p></p><img><blockquote>/   /</blockquote><h2></h2><p></p><img><blockquote>  /</blockquote><div class="quote"><br></div><p></p>`
        }
      return result
    },
    hashtags(){
      return JSON.parse(this.post.hashtag || "[]")
    },
    content(){
      return this.post.content
        .replace(/\.\.\/\.\.\//g,"/")
        .replace(/\/dropzone\/uploads/g,"http://service.zashare.org/dropzone/uploads")
        // .replace(/..\/..\//g,"/")
    },
    relatedPost(){
      return this.posts.slice(0,3)
    }

  }
}



